import http from './http'
import type {
    SubscriptionResponse,
    LimitsResponse,
    PlanInfo,
    UpgradeRequest,
    CreateInvoiceRequest,
    CreateInvoiceResponse,
} from '@/api/types'

export const SubscriptionCall = {
    getCurrent() {
        return http.get<SubscriptionResponse>('/subscription')
    },

    getLimits() {
        return http.get<LimitsResponse>('/subscription/limits')
    },

    getHistory() {
        return http.get<SubscriptionResponse[]>('/subscription/history')
    },

    getPlans() {
        return http.get<PlanInfo[]>('/subscription/plans')
    },

    createInvoice(payload: CreateInvoiceRequest) {
        return http.post<CreateInvoiceResponse>(
            '/subscription/create-invoice',
            payload
        )
    },

    upgrade(payload: UpgradeRequest) {
        return http.post<SubscriptionResponse>(
            '/subscription/upgrade',
            payload
        )
    },
}