import http from './http'
import type {
    PageListResponse,
    PageResponse,
    CreatePageRequest,
    UpdatePageRequest,
    BlockInfoResponse,
} from '@/api/types'

export const PagesCall = {
    getPages(params?: { page?: number; size?: number }) {
        return http.get<PageListResponse>('/pages', { params })
    },

    getPage(pageId: string) {
        return http.get<PageResponse>(`/pages/${pageId}`)
    },

    createPage(payload: CreatePageRequest) {
        return http.post<PageResponse>('/pages', payload)
    },

    updatePage(pageId: string, payload: UpdatePageRequest) {
        return http.put<PageResponse>(`/pages/${pageId}`, payload)
    },

    deletePage(pageId: string) {
        return http.delete<void>(`/pages/${pageId}`)
    },

    searchPages(
        query: string,
        params?: { page?: number; size?: number }
    ) {
        return http.get<PageListResponse>('/pages/search', {
            params: { q: query, ...params },
        })
    },

    getBlockTypes() {
        return http.get<BlockInfoResponse>('/pages/block-types')
    },
}