import http from './http'
import type {
    BlockResponse,
    CreateBlockRequest,
    UpdateBlockRequest,
} from '@/api/types'

export const BlocksCall = {
    createBlock(pageId: string, payload: CreateBlockRequest) {
        return http.post<BlockResponse>(
            `/pages/${pageId}/blocks`,
            payload
        )
    },

    updateBlock(
        pageId: string,
        blockId: string,
        payload: UpdateBlockRequest
    ) {
        return http.put<BlockResponse>(
            `/pages/${pageId}/blocks/${blockId}`,
            payload
        )
    },

    deleteBlock(pageId: string, blockId: string) {
        return http.delete<void>(
            `/pages/${pageId}/blocks/${blockId}`
        )
    },
}